import React from 'react'
import { Avatar, Box, Link, Typography } from '@material-ui/core'
import { Link as LinkRoute } from 'react-router-dom'
import { useParams } from 'react-router-dom'
import { AppShell } from 'components/AppShell'
import { CustomError } from 'components/error/CustomError'
import { ButtonBack } from 'components/header/ButtonBack'
import { PostsList } from 'components/postsList/PostsList'
import { useUser } from 'components/user/userHooks'
import { getFullName, getProfileUrl } from 'components/user/userUtils'
import i18n from 'i18n'

export const ProfileScreen = () => {
  let { handle } = useParams()
  const user = useUser({ handle })

  if (user.isError) {
    throw new CustomError(user.error.message, { heading: i18n.userNotFound, text: i18n.userNotFoundDescription })
  }

  return (
    <AppShell header={{ leftButton: ButtonBack }}>
      {user.data && (
        <Box width={1} maxWidth={600}>
          <Box display="flex" justifyContent="center" my={1}>
            <Avatar src={user.data.avatar} style={{ height: '150px', width: '150px' }} />
          </Box>
          <Typography variant="h4">{getFullName(user.data)}</Typography>
          <Typography>@{user.data.handle}</Typography>

          <Link to={`${getProfileUrl(user.data)}/following`} color="inherit" component={LinkRoute}>
            <Typography component="span">
              <strong>{user.data.followingUserIds.length}</strong> {i18n.following}
            </Typography>
          </Link>
          <PostsList filter={{ authorUserId: [user.data.id] }} />
        </Box>
      )}
    </AppShell>
  )
}
